import React, { Component } from "react";
import { StyleSheet, css } from "aphrodite/no-important";
import { Container, Row, Col, Form, Button } from "react-bootstrap";

const styles = StyleSheet.create({
  cardsContainer: {
    marginRight: -30,
    marginTop: -30,
  },
  cardRow: {
    marginTop: 30,
    "@media (max-width: 768px)": {
      marginTop: 0,
    },
  },
  img: {
    width: 85,
    height: 85,
    paddingTop: 25,
  },
  links: {
    backgroundColor: "#fff",
    ":hover": {
      backgroundColor: "#00000",
    },
  },
  miniCardContainer: {
    flexGrow: 1,
    marginRight: 30,
    "@media (max-width: 768px)": {
      marginTop: 30,
      maxWidth: "none",
    },
  },
  todayTrends: {
    marginTop: 30,
  },
  lastRow: {
    marginTop: 30,
  },
  unresolvedTickets: {
    marginRight: 30,
    "@media (max-width: 1024px)": {
      marginRight: 0,
    },
  },
  tasks: {
    marginTop: 0,
    "@media (max-width: 1024px)": {
      marginTop: 30,
    },
  },
});
class PrepareShipment extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedItem: "Myshipments",
      results: [],
      trackingid: "",
      status: "",
      biker: "",
      loading: "",
    };
  }
  handleform(e) {
    e.preventDefault();
  }
  handleChange = (e) => {
    e.preventDefault();
    const { name, value } = e.target;
    this.setState({ [name]: value }, () => console.log(this.state));
  };
  setInputValue(property, e) {
    this.setState({
      [property]: e,
    });
    console.log(([property] = e));
  }
  async AssignBiker() {
    try {
      let res = await fetch("/assign", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          trackingid: this.state.trackingid,
          biker: this.state.biker,
        }),
      });

      let result = await res.json();
      if (result && result.success) {
        return alert("Assign Successfully");
      } else if (result && result.success === false) {
        alert(result.msg);
      }
    } catch (e) {
      console.log(e);
    }
  }
  async UpdateTracking() {
    try {
      let res = await fetch("/updatetracking", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          trackingid: this.state.trackingid,
          status: this.state.status,
        }),
      });

      let result = await res.json();
      if (result && result.success) {
        return alert("Tracking Information Updated Succesafully");
      } else if (result && result.success === false) {
        alert(result.msg);
      }
    } catch (e) {
      console.log(e);
    }
  }
  async componentDidMount() {
    window.addEventListener("resize", this.resize);

    try {
      let res = await fetch("/fetchbikers", {
        method: "post",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });
      const results = await res.json();
      this.setState({ results });
    } catch (e) {
      console.log(e);
    }
  }
  render() {
    return (
      <>
        <Container className="cont">
          <Row>
            <Col>
              <div className="big-ball">
                <div className="dashboard">
                  <div className={css(styles.content)}>
                    <div
                      style={{
                        backgroundColor: "#fff",
                        padding: "2em",
                        borderRadius: "0.5em",
                        width: "100% !important",
                        margin: "0 auto",
                      }}
                    >
                      <div className="demo-tabs">
                        <div style={{ textAlign: "center" }}>
                          <p
                            style={{
                              fontFamily: "Roboto, sans-serif",
                              color: "#00000",
                              fontSize: "20px",
                            }}
                          >
                            Assign and Update Tracking Details
                          </p>

                          <hr />
                        </div>

                        <div className="Play-Ground">
                          <section>
                            <h1>Assign a Biker</h1>
                            <Form onSubmit={this.handleform}>
                              <Form.Row>
                                <Form.Group as={Col} controlId="formGridCity">
                                  <Form.Label>Tracking Id</Form.Label>
                                  <Form.Control
                                    value={
                                      this.state.trackingid
                                        ? this.state.trackingid
                                        : ""
                                    }
                                    onChange={(e) =>
                                      this.setInputValue(
                                        "trackingid",
                                        e.target.value
                                      )
                                    }
                                    className="form-input"
                                    type="text"
                                  />
                                </Form.Group>

                                <Form.Group
                                  as={Col}
                                  controlId="exampleForm.ControlSelect1"
                                >
                                  <Form.Label>Biker</Form.Label>
                                  <Form.Control
                                    className="form-input"
                                    name="biker"
                                    onChange={this.handleChange}
                                    as="select"
                                    required
                                  >
                                    {this.state.results.map((result) => (
                                      <option>{result.username}</option>
                                    ))}
                                  </Form.Control>
                                </Form.Group>
                              </Form.Row>
                              <Button
                                className="button_login_back"
                                variant="primary"
                                type="submit"
                                onClick={() => this.AssignBiker()}
                              >
                                Assign a Biker
                              </Button>
                            </Form>

                            <h1>Update Tracking</h1>
                            <Form onSubmit={this.handleform}>
                              <Form.Row>
                                <Form.Group as={Col} controlId="formGridCity">
                                  <Form.Label>Tracking Id</Form.Label>
                                  <Form.Control
                                    value={
                                      this.state.trackingid
                                        ? this.state.trackingid
                                        : ""
                                    }
                                    onChange={(e) =>
                                      this.setInputValue(
                                        "trackingid",
                                        e.target.value
                                      )
                                    }
                                    className="form-input"
                                    type="text"
                                  />
                                </Form.Group>
                              </Form.Row>
                              <Form.Group
                                as={Col}
                                controlId="exampleForm.ControlSelect1"
                              >
                                <Form.Label>Status</Form.Label>
                                <Form.Control
                                  className="form-input"
                                  name="status"
                                  onChange={this.handleChange}
                                  as="select"
                                  required
                                >
                                  <option>collected</option>
                                  <option>transit</option>
                                  <option>success</option>
                                </Form.Control>
                              </Form.Group>
                              <Button
                                className="button_login_back"
                                variant="primary"
                                type="submit"
                                onClick={() => this.UpdateTracking()}
                              >
                                Update Tracking
                              </Button>
                            </Form>
                          </section>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </>
    );
  }
}

export default PrepareShipment;
