import React, { Component } from "react";
import { StyleSheet, css } from "aphrodite/no-important";
import { Container, Row, Col } from "react-bootstrap";

import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";

const styles = StyleSheet.create({
  cardsContainer: {
    marginRight: -30,
    marginTop: -30,
  },
  cardRow: {
    marginTop: 30,
    "@media (max-width: 768px)": {
      marginTop: 0,
    },
  },
  img: {
    width: 85,
    height: 85,
    paddingTop: 25,
  },
  links: {
    backgroundColor: "#fff",
    ":hover": {
      backgroundColor: "#00000",
    },
  },
  miniCardContainer: {
    flexGrow: 1,
    marginRight: 30,
    "@media (max-width: 768px)": {
      marginTop: 30,
      maxWidth: "none",
    },
  },
  todayTrends: {
    marginTop: 30,
  },
  lastRow: {
    marginTop: 30,
  },
  unresolvedTickets: {
    marginRight: 30,
    "@media (max-width: 1024px)": {
      marginRight: 0,
    },
  },
  tasks: {
    marginTop: 0,
    "@media (max-width: 1024px)": {
      marginTop: 30,
    },
  },
});
class ContentComponent extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedItem: "Myshipments",
      results: [],
    };
  }
  async componentDidMount() {
    window.addEventListener("resize", this.resize);

    try {
      let res = await fetch("/fetchpickup", {
        method: "post",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });
      const results = await res.json();
      this.setState({ results });
    } catch (e) {
      console.log(e);
    }
  }
  render() {
    return (
      <>
        <Container className="cont">
          <Row>
            <Col>
              <div className="big-ball">
                <div className="dashboard">
                  <div className={css(styles.content)}>
                    <div
                      style={{
                        backgroundColor: "#fff",
                        padding: "2em",
                        borderRadius: "0.5em",
                        width: "100% !important",
                        margin: "0 auto",
                      }}
                    >
                      <div className="demo-tabs">
                        <div style={{ textAlign: "center" }}>
                          <p
                            style={{
                              fontFamily: "Roboto, sans-serif",
                              color: "#00000",
                              fontSize: "20px",
                            }}
                          >
                            PICKUP REQUESTS
                          </p>

                          <hr />
                        </div>

                        <div className="Play-Ground">
                          <section>
                            <div
                              className="jss53 jss55"
                              style={{ width: "30% !important" }}
                            >
                              <TableContainer component={Paper}>
                                <Table
                                  className={styles.table}
                                  aria-label="simple table"
                                >
                                  <TableHead>
                                    <TableRow>
                                      <TableCell>tracking id</TableCell>
                                      <TableCell align="right">From</TableCell>
                                      <TableCell align="right">To</TableCell>
                                      <TableCell align="right">
                                        Sender Info
                                      </TableCell>
                                      <TableCell align="right">
                                        Receivers Info&nbsp;
                                      </TableCell>
                                      <TableCell align="right">
                                        Item Type
                                      </TableCell>
                                      <TableCell align="right">
                                        Payment Type
                                      </TableCell>
                                      <TableCell align="right">Biker</TableCell>
                                      <TableCell align="right">
                                        Status&nbsp;
                                      </TableCell>
                                    </TableRow>
                                  </TableHead>
                                  <TableBody>
                                    {this.state.results.map((result) => (
                                      <TableRow key={result.trackingid}>
                                        <TableCell component="th" scope="row">
                                          {result.trackingid}
                                        </TableCell>
                                        <TableCell align="right">
                                          {result.fromstate}
                                        </TableCell>
                                        <TableCell align="right">
                                          {result.tostate}
                                        </TableCell>
                                        <TableCell align="right">
                                          {result.sendername}-
                                          {result.senderphone}-
                                          {result.plocation}
                                        </TableCell>
                                        <TableCell align="right">
                                          {result.rname}-{result.rphone}-
                                          {result.dlocation}
                                        </TableCell>
                                        <TableCell align="right">
                                          {result.itemtype}
                                        </TableCell>
                                        <TableCell align="right">
                                          {result.amount} - {result.payment}
                                        </TableCell>
                                        <TableCell align="right">
                                          {result.bikerinfo}
                                        </TableCell>
                                        <TableCell align="right">
                                          {result.status}
                                        </TableCell>
                                      </TableRow>
                                    ))}
                                  </TableBody>
                                </Table>
                              </TableContainer>
                            </div>
                          </section>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </>
    );
  }
}

export default ContentComponent;
