import React, { Component } from "react";
import { StyleSheet, css } from "aphrodite/no-important";
import { Container, Row, Col, Form, Button } from "react-bootstrap";

const styles = StyleSheet.create({
  cardsContainer: {
    marginRight: -30,
    marginTop: -30,
  },
  cardRow: {
    marginTop: 30,
    "@media (max-width: 768px)": {
      marginTop: 0,
    },
  },
  img: {
    width: 85,
    height: 85,
    paddingTop: 25,
  },
  links: {
    backgroundColor: "#fff",
    ":hover": {
      backgroundColor: "#00000",
    },
  },
  miniCardContainer: {
    flexGrow: 1,
    marginRight: 30,
    "@media (max-width: 768px)": {
      marginTop: 30,
      maxWidth: "none",
    },
  },
  todayTrends: {
    marginTop: 30,
  },
  lastRow: {
    marginTop: 30,
  },
  unresolvedTickets: {
    marginRight: 30,
    "@media (max-width: 1024px)": {
      marginRight: 0,
    },
  },
  tasks: {
    marginTop: 0,
    "@media (max-width: 1024px)": {
      marginTop: 30,
    },
  },
});
class PrepareShipment extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedItem: "Myshipments",
      results: [],
      username: "",
      password: "",
      loading: "",
    };
  }
  handleform(e) {
    e.preventDefault();
  }

  setInputValue(property, e) {
    this.setState({
      [property]: e,
    });
    console.log(([property] = e));
  }
  async CreateUser() {
    try {
      let res = await fetch("/createbiker", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          username: this.state.username,
          password: this.state.password,
        }),
      });

      let result = await res.json();
      if (result && result.success) {
        return alert("User Created");
      } else if (result && result.success === false) {
        alert(result.msg);
      }
    } catch (e) {
      console.log(e);
    }
  }
  render() {
    return (
      <>
        <Container className="cont">
          <Row>
            <Col>
              <div className="big-ball">
                <div className="dashboard">
                  <div className={css(styles.content)}>
                    <div
                      style={{
                        backgroundColor: "#fff",
                        padding: "2em",
                        borderRadius: "0.5em",
                        width: "100% !important",
                        margin: "0 auto",
                      }}
                    >
                      <div className="demo-tabs">
                        <div style={{ textAlign: "center" }}>
                          <p
                            style={{
                              fontFamily: "Roboto, sans-serif",
                              color: "#00000",
                              fontSize: "20px",
                            }}
                          >
                            Assign and Update Tracking Details
                          </p>

                          <hr />
                        </div>

                        <div className="Play-Ground">
                          <section>
                            <h1>Create a Biker</h1>
                            <Form onSubmit={this.handleform}>
                              <Form.Row>
                                <Form.Group as={Col} controlId="formGridCity">
                                  <Form.Label>Username</Form.Label>
                                  <Form.Control
                                    value={
                                      this.state.username
                                        ? this.state.username
                                        : ""
                                    }
                                    onChange={(e) =>
                                      this.setInputValue(
                                        "username",
                                        e.target.value
                                      )
                                    }
                                    className="form-input"
                                    type="text"
                                  />
                                </Form.Group>

                                <Form.Group as={Col} controlId="formGridZip">
                                  <Form.Label>Password</Form.Label>
                                  <Form.Control
                                    value={
                                      this.state.password
                                        ? this.state.password
                                        : ""
                                    }
                                    onChange={(e) =>
                                      this.setInputValue(
                                        "password",
                                        e.target.value
                                      )
                                    }
                                    className="form-input"
                                    type="password"
                                  />
                                </Form.Group>
                              </Form.Row>
                              <Button
                                className="button_login_back"
                                variant="primary"
                                type="submit"
                                onClick={() => this.CreateUser()}
                              >
                                Create a Biker
                              </Button>
                            </Form>
                          </section>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </>
    );
  }
}

export default PrepareShipment;
